import {NgModule} from '@angular/core';
import {Routes,RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import { ContactDetailComponent } from './contact-detail/contact-detail.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import {contactsModule} from './contacts/contact.module';
const routeConfig:Routes =[

 
    {path: 'detail/:id/:name/:phoneNumber',component:ContactDetailComponent},
    {path:'',redirectTo:'/contacts',pathMatch:'full'},
    {path:'**',component:PageNotFoundComponent} //đối với những route khác
  ];
  @NgModule({

    imports: [ 
      contactsModule,
      RouterModule.forRoot(routeConfig),
      CommonModule
    ],
    declarations:[
        PageNotFoundComponent,
        ContactDetailComponent
    ],
    exports:[RouterModule]
  })
  export class AppRoutingModule{Ơ}


