import {Component,OnInit} from '@angular/core';
import {FormGroup,FormControl,FormBuilder,Validator, Validators} from '@angular/forms';

@Component({
    selector:'app-sign-up',
    template:`
    <form (ngSubmit)="onSubmit();" [formGroup]="formSignUp">
        <input placeholder="Email" formControlName="Email">
        <p *ngIf="formSignUp.get('Email').invalid && formSignUp.get('Email').touched">Email is requied</p>
        <br><br>
        <input type="Password" placeholder="Password" formControlName="Password">
        <br><br>
        <div formGroupName="Subject">
            <label><input type="checkbox" name ="NodeJs" formControlName="NodeJs">NodeJs</label>
            <label><input type="checkbox" name ="Angualar" formControlName="Angualar">Angualar</label>
            <label><input type="checkbox" name ="ReactJs" formControlName="ReactJs">ReactJs</label>                                                                

        </div>
        
        <br><br>
        <button [disabled]="formSignUp.invalid">Submit</button>
       
    </form>
    <code>{{formSignUp.controls.Email.errors | json}}</code>
    `
})
export class SignUpComponent implements OnInit{

  

    formSignUp:FormGroup;

    
    constructor(private fb:FormBuilder){

      
    }
  
    ngOnInit(): void {
        this.formSignUp = this.fb.group({
            Email:['',[Validators.email, gmailValidator]] ,
            Password:'',
            Subject:this.fb.group({
                NodeJs: false,
                Angualar:true,
                ReactJs: true            
            })
        });
    }

    onSubmit(){
        console.log(this.formSignUp.value);
    }

}
function gmailValidator(fromControl:FormControl){
    if(fromControl.value.includes('@gmail.com'))
    {
        return null;
    }
    return  {gmail:true};
}


