import {Component} from '@angular/core';
import {SingInService} from './sign-in.service';
@Component({
    selector:'app-sign-in',
    // template:`
    // <input placeholder="Email" [(ngModel)]="email">
    // <br><br>
    // <input type="Password" placeholder="Password" [(ngModel)]="passWord">
    // <br><br>
    // <button (click)="onSubmit();">Submit</button>
        // <p *ngIf="formSignIn.controls.email?.errors?.required">Email is required</p>
    // `
    template:`
    <form (ngSubmit)="onSubmit(formSignIn);" #formSignIn="ngForm">
        <input placeholder="Email"
            ngModel
            #txtEmail="ngModel"
            name="email" 
            required
            email
        >  
        <p *ngIf="txtEmail.touched && txtEmail.errors?.required">
            Email is required
        </p>
        <p *ngIf="txtEmail.touched && txtEmail.errors?.email">
            Email is not valid
        </p>
        <br><br>
        <input     
            type="Password" 
            placeholder="Password" 
            ngModel name="passWord" 
            minlength="6"
            #txtPassword ="ngModel"
            pattern="[a-z]*"
            >
        <p *ngIf="formSignIn.controls.Password?.errors?.required">
            Email is required
        </p>
        <br><br>

        <div ngModelGroup="Subjects">
            <label><input type="checkbox" [ngModel]="false" name ="NodeJs">NodeJs</label><br>
            <label><input type="checkbox" [ngModel]="false" name ="Angular">Angular</label><br>
        </div>
      
        <button [disabled]="formSignIn.invalid" >Submit</button>
    </form>
   <p>{{txtEmail.errors|json}}</p>
   <p>{{txtPassword.errors|json}}</p>
   <p>{{formSignIn.value|json}}</p>
    `,
    styleUrls: ['./sign-in.css'],
    providers:[SingInService]
})
export class SignInComponent{
    //email = '';
    //passWord = '';
    //có directrive formsignin thì k cần object
    constructor (private signinService:SingInService){}
    onSubmit(formSignIn){
      
       this.signinService.getPost(formSignIn.value)
        .then(res=>console.log(res))
        .catch(err=>console.log(err))
    }

}


