import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-words',
  templateUrl: './words.component.html',
  styleUrls: ['./words.component.css']
})
export class WordsComponent implements OnInit {
  NewEn = "";
  NewVn = "";
  filterModule="XemTatCa";
  
  isShowForm=false;
  isShowBtn = true;
   arrWords = [
    { id: 1, en: 'action', vn: 'hành động', memorized: true },
    { id: 2, en: 'actor', vn: 'diễn viên', memorized: false },
    { id: 3, en: 'activity', vn: 'hoạt động', memorized: true },
    { id: 4, en: 'active', vn: 'chủ động', memorized: true },
    { id: 5, en: 'bath', vn: 'tắm', memorized: false },
    { id: 6, en: 'bedroom', vn: 'phòng ngủ', memorized: true }
];
  constructor() { }


  ngOnInit() {
   
  }
  addWord(){
    //Thêm vào cuối Push, đầu: unShift
    this.arrWords.unshift({
      id:this.addWord.length + 1,
      en:this.NewEn,
      vn:this.NewEn,
      memorized:true
    });
    this.NewEn = "";
    this.NewVn = "";
   
    this.isShowForm = false;
  }

  showForm(){
    this.isShowForm = true;
    this.isShowBtn = false;
  }
  isDelete(id:number){
  
   const index = this.arrWords.findIndex(w=>w.id==id);
   console.log("id:"+id+"  index:"+ index);
   this.arrWords.splice(index,1);
   
  }
  myFunction()
  {
    console.log(this.filterModule);
  }
  getShowStatus(memorized:boolean){
    const dkXemTatCa = this.filterModule === 'XemTatCa';
    const dkDaNho = this.filterModule==='xemdathuoc' && memorized;
    const dkChuaThuoc = this.filterModule==='False' && !memorized;
    return dkXemTatCa || dkDaNho ||dkChuaThuoc;
  
  }

}



