import { Component, OnInit } from '@angular/core';
import {Http} from '@angular/http';
import 'rxjs/add/operator/toPromise';
import {WeathertempService} from './weatherTemp';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css'],
  providers:[WeathertempService]
})
export class WeatherComponent implements OnInit {

  txtcityName:string
  cityName=''
  Temp= null
  isLoading = false
  GetMessage:string
  constructor(private weatherTemp:WeathertempService){}
  ngOnInit() {
    // this.weatherTemp.getTemp(this.cityName)
    // .then(temp=>this.Temp = temp)
    // .then(err=>console.log(err));
  }
  SearchTemp()
  {
    this.isLoading = true;
    this.weatherTemp.getTemp(this.txtcityName)
    .then( temp=>
      {
        this.Temp = temp;
        this.cityName = this.txtcityName;
        this.isLoading = false;
      }
      )
    .catch(err=>
      {
        alert('Can not file your City');
        this.isLoading = false;
        this.cityName = '';
        this.txtcityName = ''
      });
  }
  getMessage()
  {
    if(this.isLoading)
    {
      return 'Loading...'
    }
    return this.cityName === '' ? 'Enter your city' :this.cityName +' is now '+ this.Temp;
  }

}



